import { Component, OnInit, Input, Directive, TemplateRef, ContentChild, Output, EventEmitter, ViewChild, ElementRef, Renderer2, ViewContainerRef } from '@angular/core';
import { IResizeEvent } from '@farris/ui-draggable';

@Directive({
  selector: '[listNavHeader]'
})
export class listNavHeaderTemplateDirective {
  constructor(public template: TemplateRef<any>) { }
}

@Directive({
  selector: '[listNavContent]'
})
export class listNavContentTemplateDirective {
  constructor(public template: TemplateRef<any>) { }
}

@Directive({
  selector: '[listNavFooter]'
})
export class listNavFooterTemplateDirective {
  constructor(public template: TemplateRef<any>) { }
}

@Component({
  selector: 'farris-list-nav',
  templateUrl: './list-nav.component.html',
  styleUrls: ['./list-nav.component.scss']
})
export class ListNavComponent implements OnInit {
  @ContentChild(listNavHeaderTemplateDirective, { read: TemplateRef }) HeaderTemplate: TemplateRef<any>;
  @ContentChild(listNavContentTemplateDirective, { read: TemplateRef }) NavContentTemplate: TemplateRef<any>;
  @ContentChild(listNavFooterTemplateDirective, { read: TemplateRef }) NavFooterTemplate: TemplateRef<any>;
  /**æ¶èµ·æå ç¶æåå */
  @Output('navStateChange') navStateChange = new EventEmitter();

  /** emitted when start resizing */
  @Output() rzStart = new EventEmitter<IResizeEvent>();

  /** emitted when start resizing */
  @Output() rzResizing = new EventEmitter<IResizeEvent>();

  /** emitted when stop resizing */
  @Output() rzStop = new EventEmitter<IResizeEvent>();

  // ææ½åç´ 
  @ViewChild('draggableWrapper') draggableWrapper: ElementRef;
  /**listnav åç§°*/
  @Input() title: string;
  /**
   * listnavå®½åº¦ 
   * ä¸æ¯æç¾åæ¯ï¼å ä¸ºæ­¤å¤çå®½åº¦è¢«ç¨å¨åé¨æ¯å¤é¨
   */
  @Input() listNavWidth: number = 218;

  // åå§æ¶èµ·ç¶æ
  private initCollapse = false;
  // åå§æ¶ï¼æ¯å¦éèNav
  @Input()
  set hideNav(value) {
    if (value !== undefined && value !== null) {
      if (this.initCollapse !== value) {
        this.initCollapse = value;
        // this.toggleInitCollapseState();
      }
    }
  }
  get hideNav() {
    return this.initCollapse;
  }

  // æ¾ç¤ºå¥å£
  @Input() showEntry = true;
  //--æªä½¿ç¨
  @Input() hover: boolean = false;
  // æ§ä»¶ç¦ç¨
  @Input() disabled: boolean = false;
  @Input() position: 'left' | 'right'|'top'|'bottom' = 'left';

  // ææ½å°æä¸ªå¼æ¶ï¼èªå¨æ¶èµ·çéå¼ãå¦æéå¼è®¾ç½®å°äº0ï¼åä¸å¤çèªå¨æ¶èµ·
  @Input() resizeCollapseThreshold = 20;

  // å¯ç¨ææ½åè½
  @Input() enableDraggable = false;
  // è®¾ç½®ææ½çæå°å¼
  @Input() rzMinWidth:number=null;
  // è®¾ç½®ææ½çæ¹å
  @Input() rzHandles: 'e' | 's' | 'n' | 'w' = 'e';
  // å¤çå³èåå¨ä¸­ï¼å¦å¤ä¸å¤æiframeï¼iframeå½±åææ½çæ§è½
  private _relatedIframeParent: string | ElementRef | any;
  // è®°å½çå®çiframeç¶å®¹å¨
  private _relatedContainer = null;
  // æ è®°iframeç¶å®¹å¨çæ ·å¼å±æ§positionçå¼
  private _relatedContainerPosition = "";
  // è®°å½iframeå¨ææ½æ¶çé®ç½©å±
  private _relatedIframeOverlay = null;
  // æ è®°iframeé®ç½©å±æ¯å¦å·²æ¾ç¤º
  private _iframeOverlayShownFlag = false;
  @Input()
  set relatedIframeParent(value: string | ElementRef | any) {
    this.getRelatedIframeParent(value);
  }
  get relatedIframeParent() {
    return this._relatedContainer;
  }

  // collapseçå¨ç»æ ·å¼å
  private animateCls = "splitter-pane-collapse-animate";
  // æ è®°æ¯å¦éè¦æ´æ°åé
  private updateParamsFlag = false;
  // æ è®°ææ½æ¶ä¸æ¬¡çå¤§å°
  private resizeDistance = -1;
  // å½ååç´ 
  private el:HTMLElement;
  /**
   *  åæé å½æ°æ¯æ²¡æåæ°ç
   * @param elementRef 
   * @param renderer 
   */
  constructor(private elementRef: ElementRef, private renderer: Renderer2) {
   
  }

  ngOnInit() {
    if(this.elementRef){
      this.compatibleEl();
    }
  }
  ngAfterViewInit() {
    if(!this.el){
      this.compatibleEl();
  }   
  }
  /**
   * å¼å®¹æ§è¡¨ååæ³
   */
  private compatibleEl() {
    this.el = this.elementRef.nativeElement;
    // è®¾ç½®äºiframeå®¹å¨ï¼ä½æ¯æ²¡æåå»ºé®ç½©
    if (this._relatedIframeParent && !this._relatedIframeOverlay) {
      this.getRelatedIframeParent(this._relatedIframeParent);
    }
  }
  /**
   * ç¹å»åæ
   * @param event 
   */
  toggle(event) {
    event&&event.stopPropagation();
    if (this.disabled) {
      return;
    }
    // åå§æ²¡æå ä¸è¿ä¸ªæ ·å¼ï¼æ¯é¿ååå§è®¾ç½®æ¶èµ·çæ¶åï¼çå°æåæçå¨ç»
    this.renderer&&this.renderer.addClass(this.el.querySelector('.f-list-nav-in'), this.animateCls);
    this.initCollapse=!this.initCollapse;
    this.navStateChange.emit({ 'showNav': !this.hideNav });
  }



  //--æªä½¿ç¨
  hoverToggle(event) {
    this.hideNav = !this.hideNav;
    this.navStateChange.emit({ 'showNav': !this.hideNav });
  }
  /**
    *  ææ½çååä¸­äºä»¶
    *  å¦æè¶è¿éå¼ï¼ä¸å¤ç
    *  ææªå¤çææ½æå¤§ãæå°å¼çé®é¢
    */
  onRZResizing(pos) {
    if (pos && pos["size"]) {
      let changeProp = this.getStyleProp();
      let resizeDirection = "";
      if (this.resizeDistance >= 0) {
        // å¼å§è®°å½å
        // æ­¤æ¬¡å¼æ¯ä¸æ¬¡è®°å½å¤§ï¼è¯´æå¨expand
        resizeDirection =
          pos["size"][changeProp] > this.resizeDistance
            ? "expand"
            : "collapse";
      }
      this.resizeDistance = pos["size"][changeProp];
      if (resizeDirection == "expand" && this.initCollapse) {
        this.initCollapse = false;
      }
      if (
        pos["size"][changeProp] > this.resizeCollapseThreshold ||
        !resizeDirection
      ) {
        // ææ½éåº¦å¿«çæ¶åï¼ä¼æ¼æè®¡ç®çæ¶æº
        return;
      }

      if (this.resizeCollapseThreshold > 0) {
        // è®¾ç½®äºèªå¨åæéå¼
        if (!this.initCollapse && resizeDirection == "collapse") {
          // å¤çææ½åæè¿ç¨ä¸­ï¼èªå¨æ¶èµ·ï¼ä¸å¤çå±å¼è¿ç¨ä¸­çï¼èªå¨å±å¼
          if (
            pos["actions"] &&
            pos["actions"].hasOwnProperty("stopResize")
          ) {
            pos["actions"]["stopResize"]();
          }
          this.toggle(null);
        }
      } else {
        // æªè®¾ç½®èªå¨åæéå¼ï¼éè¦åç¬å¤ç å³å°æ¶èµ·æ¶ç¶æãå³å°å±å¼æ¶ç¶æ
        if (resizeDirection == "collapse") {
          if (pos["size"][changeProp] < 3 && !this.initCollapse) {
            this.initCollapse = true;
          }
        }
      }
    }
    this.rzResizing.emit(pos);
  }
  // ææ½çå¼å§çäºä»¶
  onRZStart(pos) {
    // ç¦ç¨åæ
 
      if (this.draggableWrapper.nativeElement.className.indexOf(this.animateCls) > -1) {
        this.renderer.removeClass(this.draggableWrapper.nativeElement, this.animateCls);
      }
    // å¦ææiframeé®ç½©å¹¶ä¸æ²¡ææ¾ç¤º
    if (this._relatedIframeOverlay && !this._iframeOverlayShownFlag) {
      if (
        ["relative", "absolute"].indexOf(
          this._relatedContainerPosition
        ) < 0
      ) {
        this.renderer.setStyle(
          this._relatedContainer,
          "position",
          "relative"
        );
      }
      this.renderer.setStyle(
        this._relatedIframeOverlay,
        "display",
        "block"
      );
      this._iframeOverlayShownFlag = true;
    }
    if (
      !this.updateParamsFlag &&
      pos["actions"] &&
      pos["actions"].hasOwnProperty("updateParams")
    ) {
      pos["actions"]["updateParams"]({ fixedEdge: true });
      this.updateParamsFlag = true;
    }
    this.rzStart.emit(pos);
  }
  // ææ½çåæ­¢äºä»¶
  onRZStop(pos) {
    this.resizeDistance = -1;
    // å¦ææiframeé®ç½©å¹¶ä¸æ¾ç¤ºäº
    if (this._relatedIframeOverlay && this._iframeOverlayShownFlag) {
      if (
        ["relative", "absolute"].indexOf(
          this._relatedContainerPosition
        ) < 0
      ) {
        this.renderer.setStyle(
          this._relatedContainer,
          "position",
          this._relatedContainerPosition
        );
      }
      this.renderer.setStyle(
        this._relatedIframeOverlay,
        "display",
        "none"
      );
      this._iframeOverlayShownFlag = false;
    }
    this.rzStop.emit(pos);
  }
  /**
 * å¸å±æåä¸ä¸æ ·ï¼å½±åå°æ¯è®¾ç½®å®½åº¦è¿æ¯é«åº¦
 */
  private getStyleProp() {
    let hori = ["left", "right"].findIndex(
      item => item == this.position
    );
    return hori > -1 ? "width" : "height";
  }

  /**
   * è§èè·åiframeæå¨çç¶å®¹å¨
   */
  private getRelatedIframeParent(content) {
    if (!content || !this.elementRef) {
      this._relatedContainer = null;
    }
    if (content instanceof ElementRef) {
      this._relatedContainer = content.nativeElement;
    } else if (typeof content == "string") {
      this._relatedContainer = this.elementRef.nativeElement.parentElement.querySelector(
        content
      );
    } else {
      this._relatedContainer = content;
    }
    if (this._relatedContainer) {
      this._relatedContainerPosition = this._relatedContainer["style"][
        "position"
      ];
      // æé é®ç½©å±
      this._relatedIframeOverlay = this.renderer.createElement("div");
      this._relatedIframeOverlay.className = "f-utils-absolute-all";
      this.renderer.setStyle(
        this._relatedIframeOverlay,
        "display",
        "none"
      );
      this.renderer.appendChild(
        this._relatedContainer,
        this._relatedIframeOverlay
      );
    }
  }
}
